.app_dark {
  --white: rgba(255, 255, 255, 0.1);
  --bgcolor: #1d1d1d;
  --hover: rgba(255, 134, 142, 0.2);
  --active: #ff868e;
  --text: white;
  --form: rgba(255, 255, 255, 0.5);
}
.app_light {
  --form: white;
  --text: black;
  --white: #ffffff;
  --bgcolor: #e5e5e5;
  --hover: #fbe0dc;
  --active: #ff868e;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.left {
  display: flex;
  align-items: center;
  justify-content: left;
  width: max-content;
}

.button {
  width: 146px;
  height: 40px;
  background: var(--active);
  border-radius: 10px;
  border: none;
  font-family: 'Jost';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  /* identical to box height, or 150% */
  text-align: center;
  letter-spacing: 2px;

  color: #ffffff;
}

.back {
  width: 40px;
  height: 40px;
  background-color: var(--hover);
  background-image: url(../../images/wing_default.svg);
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 5px;
  border-radius: 10px;
  border: none;
  font-family: 'Jost';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  /* identical to box height, or 150% */
  text-align: center;
  letter-spacing: 2px;

  color: #ffffff00;
}

.upload {
  width: 143px;
  height: 40px;
  background: var(--hover);
  border-radius: 10px;
  color: var(--active);
  border: none;
  background-image: url(../../images/upload_default.svg);
  background-position: left 10px center;
  background-repeat: no-repeat;
}

.upload:hover {
  background-color: var(--active);
  color: white;
  background-image: url(../../images/upload_active.svg);
  background-position: left 10px center;
  background-repeat: no-repeat;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-top: 10px;
}

.lable {
  font-family: 'Jost';

  font-style: normal;
  font-weight: 500;
  font-size: 10px;
  line-height: 18px;
  /* identical to box height, or 180% */
  position: relative;

  color: #8c8c8c;
}

.image_container {
  width: 200px;
  height: 250px;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px;
}

.image {
  width: auto;
  height: 100%;
}

.hovered {
  display: none;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 134, 142, 0.6);
}

.image_container:hover .hovered {
  display: flex;
  align-items: center;
  justify-content: center;
}

.more {
  width: 172px;
  height: 40px;
  background: var(--active);
  border-radius: 10px;
  border: none;
  margin: 0 auto;
}

.form {
  margin-top: 5px;
  width: 100%;
  height: max-content;
  background-color: var(--gray);
  border-radius: 20px;
  display: flex;
  flex-wrap: wrap;

  padding-left: 30px;
  padding-top: 12px;

  justify-content: space-between;
  box-sizing: border-box;
}

.select {
  width: calc(90%);

  height: 40px;
  background: var(--form);
  border-radius: 10px;
  font-family: 'Jost';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height, or 150% */

  color: var(--text);

  border: none;
  display: block;
}

.select:hover {
  border: 1px solid var(--hover);
}

.scroll {
  width: 40px;
  height: 40px;
  background: var(--form);
  border-radius: 10px;
  background-image: url(../../images/scroll_default.svg);
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  margin-top: -30px;
}

form div {
  // padding-left: 30px;
  // margin-left: -30px;

  width: 50%;
  height: max-content;
}

form div div {
  display: flex;
  justify-content: space-between;
  width: 90%;
  height: 40px;
  margin-top: 30px;
}

form div div .select {
  width: 100%;
  // width: 225px;
  height: 40px;
  margin-top: -30px;
}

.scroll:hover {
  background-color: var(--hover);
}

.scroll:active {
  background-image: url(../../images/scroll_active.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-color: var(--active);
}

@media screen and (max-width: 1462px) {
  /* ... */
}
@media screen and (max-width: 1000px) {
  /* ... */
}
@media screen and (max-width: 800px) {
  /* ... */
}
@media screen and (max-width: 600px) {
  .form {
    display: block;
    height: 350px;
  }

  .form div {
    width: 100%;
  }
  .form div div {
    width: 100%;
    display: block;
    padding-right: 30px;
  }

  .form div div .select {
    display: block;
    width: 90%;
  }

  .scroll {
    display: block;
    width: 90%;
    margin-top: 20px;
  }

  .menu {
    flex-wrap: wrap;
  }

  .upload {
    width: 100%;
    margin-top: 10px;
  }
}
